import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';

import tmdbApi from '../../api/tmdbApi';
import apiConfig from '../../api/apiConfig';

import './detail.scss';
import CastList from './CastList';

import MovieList from '../../components/movie-list/MovieList';

const Detail = () => {
    

    const { category, id } = useParams();

    const { season, episode } = '1';

    const [item, setItem] = useState(null);
    const [iframeSrc, setIframeSrc] = useState('');
    const [TitleCategory, setTitleCategory] = useState('');

    useEffect(() => {
        const getDetail = async () => {
            const response = await tmdbApi.detail(category, id, {params:{}});
            setItem(response);
            window.scrollTo(0,0);

            // Determinar la fuente del iframe
            const src = category === 'movie'
                ? `https://es.oceanplay.me/embeds/${id}`
                : category === 'tv'
                ? `https://es.oceanplay.me/embeds/${id}/1/1`
                : ''; // Manejar otros casos si es necesario

            // Determinar la fuente del iframe
            const title = category === 'movie'
                ? `Ver Pelicula Online`
                : category === 'tv'
                ? `Ver Serie Online`
                : ''; // Manejar otros casos si es necesario

            setIframeSrc(src);
            setTitleCategory(title);
        }
        getDetail();
    }, [category, id]);



    return (
        <>
            {
                item && (
                    <>
                        <div className="banner" style={{backgroundImage: `url(${apiConfig.originalImage(item.backdrop_path || item.poster_path)})`}}></div>
                        <div className="mb-3 movie-content container">
                            <div className="movie-content__poster">
                                <div className="movie-content__poster__img" style={{backgroundImage: `url(${apiConfig.originalImage(item.poster_path || item.backdrop_path)})`}}></div>
                            </div>
                            <div className="movie-content__info">
                                <h1 className="title">
                                    {item.title || item.name}
                                </h1>
                                <div className="genres">
                                    {
                                        item.genres && item.genres.slice(0, 5).map((genre, i) => (
                                            <span key={i} className="genres__item">{genre.name}</span>
                                        ))
                                    }
                                </div>
                                <p className="overview">{item.overview}</p>
                                <div className="cast">
                                    <div className="section__header">
                                        <h2>Actores</h2>
                                    </div>
                                    <CastList id={item.id}/>
                                </div>
                            </div>
                        </div>
                        <div className="container">
                            <h2 className='titleCategory'>{TitleCategory}</h2>
                            <div className="section mb-3 pelicula">
                                <iframe className='movie-iframe'
                                    src={iframeSrc} title={category}
                                    frameborder="0" 
                                    allowfullscreen="" 
                                    webkitallowfullscreen="" 
                                    mozallowfullscreen="">
                                </iframe>
                            </div>
                            <div className="section mb-3">
                                <div className="section__header mb-2">
                                    <h2>Similar</h2>
                                </div>
                                <MovieList category={category} type="similar" id={item.id}/>
                            </div>
                        </div>
                    </>
                )
            }
        </>
    );
}

export default Detail;
